.wrapper {
    background-color: #fff;
    width: 100%;
    height: 90vh;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    position: relative;
}
@media (max-width: 1140px) {
    .wrapper {
        width: 100vw;
        height: 100vh;
        margin: 0;
    }
}
